import React from 'react';
import styled from 'styled-components';
import { ResetStyle, Container, Row } from '../../components/globalStyle';
import { ClockCircleOutlined } from '@ant-design/icons';
import { Timeline } from 'antd';

const Wrap = styled.div`
    overflow: unset;
    margin-top: 50px;
    list-style: none;
    position: relative;
    max-width: 1200px;
    padding: 20px;
    margin: 0 auto;

    @media (max-width : 768px){
        margin-top: 15px;
        overflow: hidden;

    }
`;

const TimelineRow = styled.div`
    padding-left: 13%;
    padding-right: 0;
    position: relative;
    z-index: 10;
    padding-bottom: 80px;

    
  .ant-timeline-item-tail{
    border-inline-start: 4px solid rgba(0, 0, 0, 0.2);
    height: 100%;
    inset-block-start: 0px;
    display: block !important;
    }
  .ant-timeline-item-head {
    padding-block: 0px;
    top: 85px;
  }

  .ant-timeline-item{
    padding-top: 20px;
  }
`;

const StyledDot = styled.div`
  width: 50px;
  height: 50px;
  background-color: #DE052E;
  border-radius: 50%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;

  
`;

const StyledChild = styled.div`
  margin-left: 40px;

  @media (max-width: 576px){
    margin-left: 15px;
  }

`;

const Time = styled.div`
    margin : 0 0 6px 20px;
`;

const H3 = styled.h3`
    font-size: 50px;
`;

const Year = styled.span`
    font-size: 16px;
    color: #888;
    display: inline-block;
    padding-left: 10px;
`;

const Content = styled.div`
    padding: 25px 40px;
    border-radius: 10px;
    margin: 0 15px;
    border: 1px solid #B7B7B7 !important;

    @media (max-width: 576px){
        padding: 15px 10px;
        margin: 0;
    }

    &::before{
        content: "";
        position: absolute;
        top: 44px;
        left: -15px;
        width: 17px;
        height: 0;
        border-style: solid;
        z-index: 1;
        border-width: 7.5px 15px 7.5px 0;
        border-color: transparent #fff transparent transparent;
    }
    
    &::after{
        border-width: 7.5px 15px 7.5px 0;
        border-color: transparent #b7b7b7 transparent transparent;
        top: 44px;
        left: -15px;
        width: 0;
        height: 0;
        border-style: solid;
        z-index: -1;
        content: "";
        position: absolute;
    }
`;

const PanelBody = styled.div`
    padding: 5px 10px;
`;

const P = styled.p`
    margin-bottom: 15px;
`;

const Li =styled.li`
    padding-left: 20px;
    margin-left: 0px !important;
    margin-bottom: 15px;
    line-height: 180%;

    &::before{
        content: "";
        position: absolute;
        left: 0;
        top: 10px;
        height: 7px;
        width: 7px;
        background: #DE052E;
        border-radius: 50%;
        display: inline-block;
    }
`;

const Img = styled.img`
    width:inherit;
    margin-bottom: 15px;

    @media (max-width :850px){
        width: 100%;
    }
`;

const TimeLine = () => {

    return (
        <div className='Timeline-section'>
            <Wrap>
                <TimelineRow>
                    <Timeline
                        items={[
                            {
                                dot: (
                                    <StyledDot>
                                        <ClockCircleOutlined style={{ fontSize: '32px' }} />
                                    </StyledDot>
                                ),
                                color: 'red',
                                children: (
                                    <StyledChild>
                                      <Time>
                                        <H3>
                                            2022
                                            <Year></Year>
                                        </H3>
                                      </Time>
                                      <Content>
                                        <PanelBody>
                                            <P></P>
                                            <ul>
                                                <Li></Li>
                                                <Li>Wang Steak PL</Li>
                                            </ul>
                                            <P></P>
                                        </PanelBody>
                                      </Content>
                                    </StyledChild>
                                  ),
                            },
                            {
                                dot: (
                                    <StyledDot>
                                        <ClockCircleOutlined style={{ fontSize: '32px' }} />
                                    </StyledDot>
                                ),
                                color: 'red',
                                children: (
                                    <StyledChild>
                                      <Time>
                                        <H3>
                                            2021
                                            <Year></Year>
                                        </H3>
                                      </Time>
                                      <Content>
                                        <PanelBody>
                                            <P></P>
                                            <ul>
                                                <Li></Li>
                                                <Li>amigo</Li>
                                                <Li>THE WANG</Li>
                                                <Li></Li>
                                            </ul>
                                            <P></P>
                                        </PanelBody>
                                      </Content>
                                    </StyledChild>
                                  ),
                            },
                            {
                                dot: (
                                    <StyledDot>
                                        <ClockCircleOutlined style={{ fontSize: '32px' }} />
                                    </StyledDot>
                                ),
                                color: 'red',
                                children: (
                                    <StyledChild>
                                      <Time>
                                        <H3>
                                            2020
                                            <Year></Year>
                                        </H3>
                                      </Time>
                                      <Content>
                                        <PanelBody>
                                            <P></P>
                                            <ul>
                                                <Li></Li>
                                                <Li>THE WANG</Li>
                                                <Li>APP269</Li>
                                                <Li></Li>
                                            </ul>
                                            <P>
                                                <Img src='https://www.wowprime.com/uploads/history/2020.jpg' />
                                            </P>
                                            <P>&nbsp;</P>
                                            <P></P>
                                        </PanelBody>
                                      </Content>
                                    </StyledChild>
                                  ),
                            },
                            {
                                dot: (
                                    <StyledDot>
                                        <ClockCircleOutlined style={{ fontSize: '32px' }} />
                                    </StyledDot>
                                ),
                                color: 'red',
                                children: (
                                    <StyledChild>
                                      <Time>
                                        <H3>
                                            2019
                                            <Year></Year>
                                        </H3>
                                      </Time>
                                      <Content>
                                        <PanelBody>
                                            <P></P>
                                            <ul>
                                                <Li>THE WANG</Li>
                                                <Li></Li>
                                                <Li></Li>
                                                <Li>ISO17025ISO22000/</Li>
                                            </ul>
                                            <P>
                                                <Img src='https://www.wowprime.com/uploads/history/2019.jpg' />
                                            </P>
                                            <P>&nbsp;</P>
                                            <P></P>
                                        </PanelBody>
                                      </Content>
                                    </StyledChild>
                                  ),
                            },
                            {
                                dot: (
                                    <StyledDot>
                                        <ClockCircleOutlined style={{ fontSize: '32px' }} />
                                    </StyledDot>
                                ),
                                color: 'red',
                                children: (
                                    <StyledChild>
                                      <Time>
                                        <H3>
                                            2018
                                            <Year></Year>
                                        </H3>
                                      </Time>
                                      <Content>
                                        <PanelBody>
                                            <P></P>
                                            <ul>
                                                <Li>12MINI</Li>
                                                <Li></Li>
                                            </ul>
                                            <P></P>
                                        </PanelBody>
                                      </Content>
                                    </StyledChild>
                                  ),
                            },
                        ]}
                    />

                </TimelineRow>
            </Wrap>
        </div>
    );
}

export default TimeLine;