import React from 'react';
import styled from 'styled-components';
import { ResetStyle, Container, Row } from '../../components/globalStyle';

const Desc = styled.div`
    padding: 100px 0 80px 0;
    margin-bottom: 80px;
    background: #F2EDEA;
`;

const TableRwd = styled.table`
    width: calc(100% + 30px);
    margin-left: -15px;

    @media (max-width: 576px) {
        width: 100%;
        margin-left: 0;
    }
`;

const Td = styled.td`
    width: 50%;
    padding: 15px;
    vertical-align: top;

    @media (max-width:1024px){
        padding: 15px 0;
    }
    @media (max-width:768px){
        display: block;
        width: 100% !important;
    }
`;

const Title = styled.p`
    line-height: 180% !important;
    color: #DE052E;
    margin-bottom: 10px;
    font-weight: bold;
    position: relative;
    padding-left: 105px;
    &::before{
        content: "";
        width: 90px;
        height: 1px;
        background: #B6B6B6;
        position: absolute;
        left: 0;
        top: 13px;
    }
`;

const SubTitle = styled.p`
    color: #000;
    font-size: 40px;
    padding-bottom: 25px;
    line-height: 180% !important;

    @media (max-width: 1200px) {
    font-size: 35px;
    }
`;

const P = styled.p`
    line-height: 180% !important;
`;

const Span = styled.span`
    font-size: 18px;
`;

const HistoryTitle = styled.h1`
    text-align: center;
    font-size: 35px;
    position: relative;
    padding-bottom: 25px;
    margin-bottom: 30px;
    line-height: 140%;
    display: flex;
    justify-content: center;

    &::after{
        content: '';
        position: absolute;
        background: #DE052E;
        height: 4px;
        width: 90px;
        left: auto !important;
        bottom: 0;
    }

    @media (max-width: 992px){
        font-size: 30px;
    }
    @media (max-width: 768px){
        font-size: 25px;
    }

`;

const History = () => {

    return (
        <div className='History-section'>
            <Desc>
                <Container>
                    <TableRwd>
                        <tbody>
                            <tr>
                                <Td>
                                    <Title>Group Introduction</Title>
                                    <SubTitle>
                                        
                                        <br />
                                        30
                                    </SubTitle>
                                </Td>
                                <Td>
                                    <P>
                                        <Span>1993</Span>
                                    </P>
                                    <P>
                                        <Span></Span>
                                    </P>
                                    <P>&nbsp;</P>
                                    <P>
                                        <Span>32</Span>
                                    </P>
                                    <P>
                                        <Span>400</Span>
                                    </P>
                                    <P>&nbsp;</P>
                                    <P>
                                        <Span>2,300</Span>
                                    </P>
                                </Td>
                            </tr>
                        </tbody>
                    </TableRwd>
                    <p>&nbsp;</p>
                </Container>
            </Desc>
            <HistoryTitle>
            
            </HistoryTitle>
        </div>
    );
}

export default History;